import util from '../../common/util.js';
export default {
	data() {
		return {
			newList:[],
			page:0,
			page_count:0
		}
	},
	onLoad() {
		this.loadMore(this.page);
	},
	onReachBottom() {
		console.log('down');
		this.loadMore(this.page=this.page+1,1);
	},
	filters:{
		filterTime: function(value){
			return util.formatTime(value)
		}
	},
	methods: {
		newsInfo(id) {
			uni.navigateTo({
				url: '../trend_info/trend_info?id='+id
			})
		},
		loadMore(page,isLoad=0){
			console.log(page);
			var _this = this;
			if(isLoad==0 || page<this.page_count){
				uni.showLoading({
					title:"加载中...",
					mask:true
				})
				uni.request({
					url:_this.serverURL + 'home/index/get_new_list',
					method:'POST',
					data:{
						category:1,
						page_num:page
					},
					success(res) {
						console.log(res);
						uni.hideLoading();
						if(res.data.code==100){
							if(isLoad==0){
								_this.newList = res.data.list;
								_this.page_count = res.data.count_page;
							} else {
								_this.newList.push.apply(_this.newList,res.data.list);
							}
						}
					},
					fail(error) {
						console.log(error)
					}
				})
			}
		}
	}
}
